<template>
    <div class="container">
        <h5 class='title'>随机测评</h5>
        <div class="card-wpt">
            <div class="card" >
                <div class="box" @click='toggle(true)'>{{hiragana}}</div>
                <div class="box" @click='toggle(false)'>{{katakana}}</div>
            </div>
            <div class="btn-group">
                <button class='btn left btn-default' @click='forget'>x</button>
                <button class='btn right btn-success' @click='remember'>√</button>
            </div>
        </div>
        <div class="bottom">
            <router-link class='btn btn-primary left' to="/">基础音图</router-link>
            <router-link class='btn btn-default right' to="/list">生词本</router-link>
        </div>
    </div>
</template>

<script>
import Data from '../data';

export default {
    data () {
        return {
            ind: [0, 0],
        }
    },
    computed: {
        hiragana () {
            return Data['hiragana'][this.ind[0]][this.ind[1]];
        },
        katakana () {
            return Data['katakana'][this.ind[0]][this.ind[1]];
        }
    },
    methods: {
        generateInd () {
            this.ind = [this.getRandom(10), this.getRandom(5)];
        },
        getRandom (max, min = 0) {
            return Math.floor(Math.random() * (max - min));
        },
        forget () {
            let str = window.localStorage.getItem('wordIndexArr');
            let obj = JSON.parse(str) || {};
            let index = this.ind[0] + '' + this.ind[1];
            if (obj[index]) {
                obj[index] ++;
            } else {
                obj[index] = 1;
            }

            window.localStorage.setItem('wordIndexArr', JSON.stringify(obj));
            this.generateInd();
        },
        remember () {
            let str = window.localStorage.getItem('wordIndexArr') || '{}';
            let obj = JSON.parse(str) || {};
            let index = this.ind[0] + '' + this.ind[1];
            if (obj[index]) {
                delete obj[index];
            }
            window.localStorage.setItem('wordIndexArr', JSON.stringify(obj));
            this.generateInd();
        },
        toggle (status) {
            let box = document.querySelectorAll('.box');
            if (status) {
                box[0].style.transform = "rotateY(180deg)";
                box[1].style.transform = "rotateY(0deg)";
            } else {
                box[1].style.transform = "rotateY(-180deg)";
                box[0].style.transform = "rotateY(0deg)";
            }
        }
    },
    created () {
        this.generateInd();
    }
}
</script>

<style scoped>
.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.card-wpt{
    width: 50%;
    margin: 0 auto;
}
.card{
    width: 100%;
    height: 50vw;
    line-height: 50vw;
    font-size: 7rem;
    text-align: center;
    position: relative;
    perspective: 1500;
    -webkit-perspective: 1500;
    -moz-perspective: 1500;
}
.box{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 1s;
    backface-visibility: hidden;
    cursor: pointer;
    border: 2px dashed #999;
    background: #FAF9DE;
}
.box:last-child{
    transform: rotateY(-180deg);
}
.btn-group{
    margin-top: 1rem;
    overflow: hidden;
}
.btn{
    background: #fff;
    padding: 4px 10px;
    border: 1px solid #aaa;
    box-sizing: content-box;
    border-radius: 3px;
    font-size: .8rem;
}
.btn-danger{
  background: red;
  border-color: darkred;
  color: #fff;
}
.btn-success{
    background: #73d13d;
    border-color: green;
    color: #fff;
}
.btn-primary{
    background: darkcyan;
    border-color: #777;
    color: #eee;
}
.title{
    background: darkcyan;
    padding: 10px 20px;
    color: #fff;
    font-weight: 100;
}
.bottom{
    padding: 0 10vw 10px;
}
</style>





